/* Reset some default browser styles */
body,
h1,
h2,
h3,
p {
  margin: 0;
  padding: 0;
}

/* Basic styling for the entire page */
body {
  font-family: Arial, sans-serif;
  background-color: #f0f0f0;
}

header {
  background-color: #333;
  color: #fff;
  text-align: center;
  padding: 20px 0;
}

nav ul {
  list-style: none;
  text-align: center;
}

nav li {
  display: inline;
  margin-right: 20px;
}

nav a {
  text-decoration: none;
  color: #333;
  font-weight: bold;
}

section {
  padding: 40px;
  background-color: #fff;
  margin: 20px;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}

.project {
  text-align: center;
}

.project img {
  width: 100%;
  max-width: 400px;
  height: auto;
}

/* Media query for responsive design */
@media screen and (max-width: 768px) {
  header {
    font-size: 24px;
  }

  nav ul {
    text-align: center;
  }

  nav li {
    display: block;
    margin-bottom: 10px;
  }

  section {
    padding: 20px;
    margin: 10px;
  }
}
